<template>
  <div id="List">
    <el-table
      :data="tableData"
      height="550"
      border
      style="width: 100%"
    show-summary
       :summary-method="getSummaries"
      :default-sort="{ prop: 'sizeValue', order: 'descending' }"
    >
      <el-table-column prop="name" label="" sortable>
      </el-table-column>
      <el-table-column prop="size" label="" sortable>
      </el-table-column>
      <el-table-column prop="type" label="" sortable>
      </el-table-column>
      <el-table-column prop="pkgName" label="" sortable>
      </el-table-column>
      <el-table-column prop="extInfo" label="">
      </el-table-column>
      <el-table-column prop="level" label="" sortable>
      </el-table-column>
      <el-table-column prop="useCount" label="" sortable>
      </el-table-column>
      <el-table-column
        prop="useCountImmediate"
        label=""
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="libCount"
        label=""
        sortable
        width="180"
      >
      </el-table-column>
      <el-table-column prop="sizeValue" label="" sortable width="180">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'List',
  data () {
    return {
      tableData: []
    }
  },
  mounted () {
    axios.get('../data/list.json').then((resp) => {
      console.log(resp.data.data)
      this.tableData = resp.data.data
    })
  },
  methods: {
    getSummaries (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = ''
          return
        }
        const values = data.map(item => Number(item[column.property]))
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr)
            if (!isNaN(value)) {
              return prev + curr
            } else {
              return prev
            }
          }, 0)
          sums[index] += ' '
        } else {
          sums[index] = 'N/A'
        }
      })

      return sums
    }
  }
}
</script>
