<template>
  <div>
    <div id="3d-graph" class="three-graph"></div>
    <!--  -->
    <div id="indicator">
      <!-- item  index  -->
      <div v-for="(name, index) in names" :key="index">
        <span
          :data-state="states[index]"
          :data-index="index"
          :style="{
            backgroundColor:
              states[index] === 'on' ? nodeColors[index] : '#aaa',
          }"
        ></span>
        {{ name }}
      </div>
    </div>
    <!--  -->
    <div id="info" v-show="selectNodeData.name !== undefined">
      <!-- <h4 :style="{ color: selectNodeData.color }">{{ selectNodeData.name }}</h4>
      <p v-for="(item, key) in selectNodeData.properties" :key="item">
        <span>{{ key }}</span>
        {{ item }}
      </p> -->
      <el-card
        :style="{ backgroundColor: selectNodeData.color }"
        class="node-card"
      >
        <div slot="header" class="clearfix">
          <span>{{ selectNodeData.name }}</span>
          <el-button
            @click="btnEdit"
            style="
              float: right;
              padding: 3px 0;
              color: #409efb;
              font-size: 15px;
            "
            type="text"
            ></el-button
          >
        </div>
        <div v-for="(item, key) in selectNodeData.properties" :key="item">
          <span style="margin-right: 8px">{{
            (nodeObjMap[key] ? nodeObjMap[key] : key) + ":"
          }}</span>
          <span style="text-align: right"
            ><b>{{ item }}</b></span
          >
        </div>
      </el-card>
    </div>
    <!--  -->
    <el-dialog :visible.sync="dialogFormVisible">
      <el-form
        :model="temp"
        label-position="right"
        label-width="86px"
        style="width: 500px; margin-left: 50px"
      >
        <el-form-item
          v-for="(value, key) in temp"
          :key="key"
          :label="nodeObjMap[key] ? nodeObjMap[key] : key"
        >
          <el-input v-model="temp[key]" :readonly="!isEdit" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">  </el-button>
        <el-button type="primary" @click="doEdit">  </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import ForceGraph3D from '3d-force-graph'
// threejs
import SpriteText from 'three-spritetext'
export default {
  name: 'Graph3d',
  props: {
    data: {
      type: Object,
      default: function () {
        return {
          nodes: [],
          links: []
        }
      }
    },
    /* eslint-disable */
    // 
    // names		
    // labels		records.json
    names: {
      type: Array,
    },
    labels: Array,
    linkTypes: Array,
  },
  mounted() {
    this.threeInit();
  },
  data() {
    return {
      // ForceGraph3D
      Graph: {},
      // threeRender()
      nodes: [],
      links: [],
      // 
      states: [],
      nodeColors: [
        "#55cccc",
        "#aaaaff",
        "#4e88af",
        "#ca635f",
        "#FFC0CB",
        "#BA55D3",
        "#1E90FF",
        "#7FFFD4",
        "#FFFF00",
      ],
      selectNodeData: {}, // 
      temp: {}, // 
      dialogFormVisible: false,
      isEdit: true,
      // 
      nodeObjMap: {
        'groupId': 'groupId',
        'artifactId': 'artifactId',
        'versionCode': '',
        'versionName': '',
        'repoUrl': '',
        'name': '',
      },
    };
  },
  watch: {
    // 
    data(newData, oldData) {
      console.log(newData, oldData);
      this.threeInit();
    },
  },
  methods: {
    // 
    update() {
      if (this.graph.length <= 20) {
        this.data = axios.get('/data/top5.json');
      } else {
        this.data = axios.get('/data/records.json');
      }
      // console.log(this.graph)
      console.log(this.graph.length);
      this.neoJsonParser(this.graph);
      // console.log(this.Graph)
      // DOMcanvas
      // 
      document.getElementById("3d-graph").innerHTML = "";
      this.threeRender();

      // 
      // this.threeUpdate({
      //   nodes: [],
      //   links: []
      // })
    },
    // 
    btnEdit() {
      this.temp = Object.assign({}, this.selectNodeData.properties); // copy obj
      this.dialogFormVisible = true;
      console.log(this.selectNodeData);
    },
    doEdit() {
      // console.log(this.data)
      let i = 0;
      // propsdata  selectNodeData
      this.selectNodeData.name = this.temp.name;
      this.selectNodeData.properties = this.temp;
      for (let node of this.data.nodes) {
        // console.log(node.id === this.selectNodeData.id)
        // console.log(node.id)
        // console.log(this.selectNodeData.id)
        if (node.id == this.selectNodeData.id) {
          // this.$set(this.data.nodes, i, this.selectNodeData)
          // this.$set(this.nodes, i, this.selectNodeData)
          this.data.nodes[i].properties = this.temp;
          this.nodes[i].properties = this.temp;
          break;
        }
        i++;
      }
      this.dialogFormVisible = false;
      this.threeInit();
      this.$message({
        message: "",
        type: "success",
      });
    },
    cancelEdit() {
      this.dialogFormVisible = false;
    },
    // d3
    threeInit() {
      this.links = this.data.links;
      this.nodes = this.data.nodes;
      this.threeRender();
      // 
      this.stateInit();
    },
    // 
    stateInit() {
      this.states = Array(this.names.length).fill("on");
    },
    threeRender() {
      // DOM
      const elm = document.getElementById("3d-graph");
      this.Graph = ForceGraph3D()(elm).graphData(this.data);

      // 
      this.Graph.height(750)
        .width(1200)
        .backgroundColor("#9dadc1")
        // 
        .nodeRelSize(7)
        .nodeColor((node) => {
          let index = 0;
          switch (node.label) {
            case this.labels[0]:
              break;
            case this.labels[1]:
              index = 1;
              break;
            case this.labels[2]:
              index = 2;
              break;
            default:
              index = 3;
              break;
          }
          return this.nodeColors[index];
        })
        // .nodeAutoColorBy('label')
        // 
        // .nodeThreeObjectExtend(true)
        .nodeThreeObject((node) => {
          const sprite = new SpriteText(node.properties.name);
          sprite.material.depthWrite = false; // make sprite background transparent
          // 
          let i = 0;
          // switch(node.label) {
          //   case this.labels[0]: break;
          //   case this.labels[1]: index = 1;break;
          //   case this.labels[2]: index = 2;break;
          //   default: index = 3;break;
          // }
          for (; i < this.labels.length; i++) {
            if (node.label === this.labels[i]) break;
          }
          sprite.color = this.nodeColors[i];
          sprite.textHeight = 8;
          return sprite;
        })
        .nodeThreeObjectExtend(true)
        .nodeLabel((node) => `${node.label}: ${node.properties.name}`)
        .nodeOpacity(0.75)
        // 
        .onNodeHover((node) => (elm.style.cursor = node ? "pointer" : null))
        .onNodeClick((node) => {
          console.log(node);
          //#info h4name
          this.$set(this.selectNodeData, "id", node.id);
          this.$set(this.selectNodeData, "name", node.properties.name);
          // 
          // let index = 0
          // switch(node.label) {
          //   case 'Enterprise': break;
          //   case 'Type': index = 1;break;
          //   case 'Region': index = 2;break;
          //   default: index = 3;break;
          // }
          let i = 0;
          for (; i < this.labels.length; i++) {
            if (node.label === this.labels[i]) break;
          }
          this.$set(this.selectNodeData, "color", this.nodeColors[i]);
          this.$set(this.selectNodeData, "properties", node.properties);
        });
      // 
      // .linkColor('#bbb')
      // .linkColor(link => {
      //   let index = 0
      //   const colors = ['#faa']
      //   switch(link.type) {
      //     case 'export': break;
      //     case 'type': index = 1;break;
      //     case 'locate': index = 2;break;
      //     default: index = 3;break;
      //   }
      //   return this.nodeColors[index]
      // })
      // .linkOpacity(1)

      // Spread nodes a little wider
      this.Graph.d3Force("charge").strength(-150);
    },
    threeUpdate(data) {
      this.Graph.graphData({
        data,
      });
    },
    // N
    createRandomGraph(N = 300) {
      return {
        nodes: [...Array(N).keys()].map((i) => ({ id: i })),
        links: [...Array(N).keys()]
          .filter((id) => id)
          .map((id) => ({
            source: id,
            target: Math.round(Math.random() * (id - 1)),
          })),
      };
    },
  },
};
</script>

