<template>
  <div class="gContainer">
    <gSearch @getData="update" />
    <!-- <threeGraph /> -->
    <Graph3d
      :data="data"
      :names="names"
      :labels="labels"
      :linkTypes="linkTypes"
    />
  </div>
</template>

<script>
// @ is an alias to /src
import gSearch from '@/components/gSearch.vue'
import Graph3d from '@/components/Graph3d.vue'

export default {
  name: 'Graph3dView',
  components: {
    gSearch,
    Graph3d
  },
  data () {
    return {
      // d3jsonParser() json 
      data: {
        nodes: [],
        links: []
      },
      names: ['', '', '', ''],
      labels: ['Enterprise', 'Type', 'Region', 'Country'],
      linkTypes: ['', 'type', 'locate', 'export']
    }
  },
  methods: {
    // 
    update (json) {
      console.log('update')
      console.log(json)
      this.d3jsonParser(json)
    },
    // json
    d3jsonParser (json) {
      const nodes = []
      const links = [] // 
      const nodeSet = [] // nodesid
      for (const item of json) {
        for (const segment of item.p.segments) {
          // data
          if (nodeSet.indexOf(segment.start.identity) === -1) {
            nodeSet.push(segment.start.identity)
            nodes.push({
              id: segment.start.identity,
              label: segment.start.labels[0],
              properties: segment.start.properties
            })
          }
          if (nodeSet.indexOf(segment.end.identity) === -1) {
            nodeSet.push(segment.end.identity)
            nodes.push({
              id: segment.end.identity,
              label: segment.end.labels[0],
              properties: segment.end.properties
            })
          }
          links.push({
            source: segment.relationship.start,
            target: segment.relationship.end,
            type: segment.relationship.type,
            properties: segment.relationship.properties
          })
        }
      }
      console.log(nodes)
      console.log(links)
      // this.links = links
      // this.nodes = nodes
      this.data = { nodes, links }
      // return { nodes, links }
    }
  }
}
</script>

